import axios from "axios";
import serverConfig from "../config";
import qs from "qs";


//  axios 
const serviceAxios = axios.create({
  baseURL: serverConfig.baseURL, // 
  timeout: 10000, // 
  withCredentials: false, //  cookie
});


// 
serviceAxios.interceptors.request.use(
  (config) => {
    //  token 
    if (serverConfig.useTokenAuthorization) {
      config.headers["Authorization"] = localStorage.getItem("logintoken"); //  token
    }
    // 
    if(!config.headers["content-type"]) { // 
      if(config.method === 'post') {
        config.headers["content-type"] = "multipart/form-data"; // post 
        //config.data = qs.stringify(config.data);
      } else {
        config.headers["content-type"] = "none"; // 
      }
    }
    console.log("", config);
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);


// 
serviceAxios.interceptors.response.use(
  (res) => {
    let data = res.data;
    //  token 
    // 
    return data;
  },
  (error) => {
    let message = "";
    if (error && error.response) {
      switch (error.response.status) {
        case 302:
          message = "";
          break;
        case 400:
          message = "";
          break;
        case 401:
          message = "";
          break;
        case 403:
          message = "";
          break;
        case 404:
          message = `: ${error.response.config.url}`;
          break;
        case 408:
          message = "";
          break;
        case 409:
          message = "";
          break;
        case 500:
          message = "";
          break;
        case 501:
          message = "";
          break;
        case 502:
          message = "";
          break;
        case 503:
          message = "";
          break;
        case 504:
          message = "";
          break;
        case 505:
          message = "HTTP ";
          break;
        default:
          message = "";
          break;
      }
    }
    return Promise.reject(message);
  }
);
export default serviceAxios;